export default {
	data() {
		return {
			
			loadingMoreFlag: true, //上滑加载开关
			loadingMore: 'loading', //上滑加载更多的状态
			hidden: true,

			pageSize: 6,
			currentPage: 1,
			list: [], //所有数据
			threeList: [], //排名前三
			// banner
			indicatorDots: true,
			autoplay: true,
			interval: 2000,
			duration: 1000,

			//标题
			rankTitle_three: '————本周热门榜首————',
			rankTitle_other: '————热销排行榜————'
		};
	},
	onReachBottom() {
		if (this.loadingMore === 'loading') {
			//防止重复加载
			return;
		}
		this.loadingMoreFlag = true;
		this.loadingMore = 'loading'; //每次上滑都要从新设置status
		setTimeout(() => {
			this.getBestProduct(); //让加载图标旋转0.8s后才请求
		}, 600);
	},
	onPullDownRefresh() {
		//下拉刷新页面将之前的数据初始化
		this.list = [];
		this.threeList = [];
		this.currentPage = 1;
		this.getBestProduct();
	},
	onLoad(e) {
		//当页面加载的时候调用接口获取内容
		setTimeout(() => {
			this.getBestProduct();
		}, 600);
	},
	methods: {
		//添加商品到购物车中
		addGoodToCar(gId) {
			let that = this;
			//是否登入
			if (that.isLogin()) {
				//获取该商品的信息
				that.baseGet(
					that.U({
						c: 'store_api',
						a: 'details',
						q: {
							id: gId
						}
					}),
					function(res) {
						//添加到购物车中
						that.basePost(
							that.U({ c: 'auth_api', a: 'set_cart' }),
							{
								productId: gId,
								merId: res.data.mer_id,
								cartNum: 1
							},
							function(res) {
								that.carNum++;
								that.isAdd=false;
							},
							function(res) {
								console.log(res);
							}
						);
					},
					function(res) {
						console.log(res);
					},
					true
				);
			}
		},
		//0.8s后隐藏加载提示的信息
		closeLoadingMoreFlag() {
			setTimeout(() => {
				this.loadingMoreFlag = false;
			}, 800);
		},
		getBestProduct() {
			var that = this;
			that.baseGet(
				that.U({
					c: 'store_api',
					a: `get_other_product`,
					q: {
						first: that.currentPage,
						limit: that.pageSize
					}
				}),
				function(res) {
					uni.stopPullDownRefresh(); //当请求成功之后将下拉刷新关闭
					if (res.data.length == 0) {
						that.loadingMore = 'noMore';
						that.closeLoadingMoreFlag(); //0.8后将状态隐藏
						return;
					}
					that.currentPage++;
					that.list = [...that.list, ...res.data];
					that.loadingMore = 'more';
					if (that.threeList.length == 0) {
						for (let i = 0; i < 3; i++) {
							that.threeList.push(that.list.shift());
						}
					}
					that.hidden = false;
				},
				function(res) {
					console.log(res);
				},
				true
			);
		},
		shopDetail(id) {
			uni.navigateTo({
				url: '/pages/list/line_details/line_details?id='+id
			});
		},
		shoppingCart(e) {
			uni.switchTab({
				url: '/pages/tabber/shopping_cart/shopping_cart'
			});
		}
	}
};
